body {
  background: yellow;
  margin: 0 auto;
  max-width: 1600px;
  font-family: "Press Start 2P", cursive;
  font-size: 36px;
}
body #image-wrapper {
  display: grid;
  width: 1500px;
  margin: 0 auto;
  grid-template-columns: repeat(5, 20%);
  grid-template-rows: repeat(5, 20%);
  height: 92vh;
}
body #image-wrapper .computer {
  grid-column-start: 3;
  grid-row-start: 1;
  align-self: end;
  margin-bottom: 20px;
}
body #image-wrapper .computer span {
  margin-bottom: 20px;
}
body #image-wrapper #computerScore, body #image-wrapper #userScore {
  display: flex;
  justify-content: center;
  transition: 0.3s all;
}
body #image-wrapper .animation {
  animation-name: scale;
  animation-duration: 1s;
}
@keyframes scale {
  0% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
body #image-wrapper .user {
  grid-column-start: 3;
  grid-row-start: 4;
  align-self: end;
  justify-self: center;
  margin-bottom: 20px;
}
body #image-wrapper .item {
  cursor: pointer;
  justify-self: center;
  transition: 0.2s all;
}
body #image-wrapper .item:hover {
  transform: scale(1.05);
}
body #image-wrapper .item.hide {
  cursor: initial;
}
body #image-wrapper .item.hide:hover {
  transform: initial;
}
body #image-wrapper .item.scissors-sizer {
  height: 167px;
  width: 178px;
}
body #image-wrapper .item.paper-sizer {
  height: 150px;
  width: 185px;
}
body #image-wrapper .item.rock-sizer {
  height: 160px;
  width: 170px;
}
body #image-wrapper #rock {
  background: url(/img/rock.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  grid-column-start: 2;
  grid-row-start: 5;
}
body #image-wrapper #rock.userChoiceStyle {
  display: flex;
  grid-column-start: 3;
  grid-row-start: 3;
}
body #image-wrapper #paper {
  background: url(/img/paper.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  grid-column-start: 3;
  grid-row-start: 5;
}
body #image-wrapper #paper.userChoiceStyle {
  display: flex;
  grid-column-start: 3;
  grid-row-start: 3;
}
body #image-wrapper #scissors {
  background: url(/img/scissors.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  grid-row-start: 5;
  grid-column-start: 4;
}
body #image-wrapper #scissors.userChoiceStyle {
  display: flex;
  grid-column-start: 3;
  grid-row-start: 3;
}
body #image-wrapper .computerrock {
  background: url(/img/rock.png);
  background-repeat: no-repeat;
  background-size: cover;
  grid-row-start: 2;
  grid-column-start: 3;
  transition: 0.2s all;
}
body #image-wrapper .computerpaper {
  background: url(/img/paper.png);
  background-repeat: no-repeat;
  background-size: cover;
  grid-row-start: 2;
  grid-column-start: 3;
  transition: 0.2s all;
}
body #image-wrapper .computerscissors {
  background: url(/img/scissors.png);
  background-repeat: no-repeat;
  background-size: cover;
  grid-row-start: 2;
  grid-column-start: 3;
  transition: 0.2s all;
}